import React, { Component } from 'react';
import './editInvoiceNavBar.css';

class EditInvoiceNavBar extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
            <div className="col-12">
                <div className="row">
                    <span className="edit-invoices-navbar-text">
                        <img alt="Dashboard-Pic" src={require('../edit-invoice-navBar/text.svg')} style={{width:"30px",height:"30px",boxShadow:"none",marginRight:"20px"}}></img>
                        Invoices
                        <img alt="right-arrow-Pic" src={require('../edit-invoice-navBar/right-arrow.svg')} style={{width:"20px",height:"20px",boxShadow:"none",marginRight:"20px",marginLeft:"20px"}}></img>
                        Edit Invoices
                        </span>
                </div>
            </div>
            </React.Fragment>
         );
    }
}
 
export default EditInvoiceNavBar;