import React, { Component } from "react";
import Chart from "../BasicChart/pieChart";
import "./styleExpenses.css";
class expensis extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid main-page-expenses w-100">
          <div className="row ">
            <div className="col-12">
              <div
                className="card card-designing"
                style={{
                  border: "none",
                  boxShadow: "5px 5px 5px 0px #E9EAEC",
                  borderRadius: "15px"
                }}
              >
                <div style={{cursor:"pointer"}} data-toggle="collapse" data-target="#income-expense-chart" className="card-body">
                  <div className="row">
                    <div className="col-10 paragraph-design">
                      <h4
                        className="mt-2"
                        style={{color: "#1e6262" }}
                      >
                        Income and expense chart
                      </h4>
                    </div>
                    <div className="col-2 ">
                      <img
                        alt="admin"
                        className={"optionIcon"}
                        src={require("../Icons/25243.svg")}
                      />
                    </div>
                  </div>
                  <div id="income-expense-chart" className="collapse row mt-2">
                    <div className="col-sm-0 col-md-3 col-lg-3 col-xl-3"></div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Chart />
                    </div>
                    <div className="col-sm-0 col-md-3 col-lg-3 col-xl-3"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default expensis;
