import React, { Component } from 'react';
import "./qutation.css";
import history from '../../Routing/history';
class QutationTable extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
                <div className="container-fluid bg-white rounded shadow-sm pb-3 pt-2 scroll-X">
                <div className="row quotation-side-border">
                    <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-6">
                    <span className="Table-Qutation">Qutations</span>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-6 text-right pt-4 pr-5">
                    <button onClick={()=>{history.push('/Quotations/Get_Quotation')}} className="btn qutation-get-quotation-btn"><i class="fas fa-plus mr-2"></i>+ Get Qutation</button>
                    </div>
                </div>
                <div className="row mt-3">
                    <div style={{padding:"0"}} className="col-12">
                        <table className="table-striped qutation-list-table">
                                <tbody>
                                    <tr className="qutation-table-body-Information-Heading-Row">
                                        <td className="text-center p-2"><span className="qutation-table-body-heading-text">Qutation#</span><span className="qutation-table-body-heading-arrow-icon"><i class="fas fa-chevron-up"><br/><i class="fas fa-chevron-down pt-1"></i></i></span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Client</span><span className="qutation-table-body-heading-arrow-icon"><i class="fas fa-chevron-up"><br/><i class="fas fa-chevron-down pt-1"></i></i></span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Issue date</span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Expiry date</span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Status</span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Value</span><span className="qutation-table-body-heading-arrow-icon"><i class="fas fa-chevron-up"><br/><i class="fas fa-chevron-down pt-1"></i></i></span></td>
                                        <td className="text-center"><span className="qutation-table-body-heading-text">Document</span></td>
                                        <td></td>
                                    </tr>
                                    <tr className="qutation-table-body-text-head-row">
                                        <td className="text-center p-4">Seller-0001</td>
                                        <td className="text-center">Muhammad Salman</td>
                                        <td className="text-center">02/03/2020</td>
                                        <td className="text-center">02/03/2020</td>
                                        <td className="text-center"><button className="qutation-qotation-button">Qutation</button></td>
                                        <td className="text-center">200$</td>
                                <td className="text-center">
                                    <img
                                        alt="docs"
                                        className="common-docs-icon"
                                        src={require('../../Assets/Icons/Common/docs.svg')}
                                    />
                                </td>
                                <div class="btn-group mt-2">
                                    <button type="button" class="btn btn-lg dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        <img className="clickimg2 shadow" alt="menu-pic" src={require("./menu.svg")}></img>
                                    </button>
                                    <div class="dropdown-menu dropdown-menu-right shadow-sm">
                                        {/* <button class="dropdown-item" type="button" data-toggle="modal" data-target="#previewBillModal">Preview</button> */}
                                        <button style={{fontSize:"13px"}} onClick={()=>{history.push('/Quotations/Edit_Quotation')}}  class="dropdown-item" type="button">Edit</button>
                                        <button style={{fontSize:"13px"}} class="dropdown-item" type="button">Delete</button>
                                    </div>
                                </div>
                                        {/* <td>
                                            <div class="dropdown">
                                            <span class="qutation-table-ellipsis-dropdown-icon bg-white" type="button" id="dropdownMenuButton" data-toggle="dropdown">
                                                <img
                                                    alt="menu"
                                                    className="qutation-table-option-icon"
                                                    src={require('../../Assets/Icons/Quotation/menu.svg')}
                                                />    
                                            </span>
                                            <div class="dropdown-menu qutation-dropdown-menu dropdown-menu-right text-center qutation-table-Action-dropdown" id="dropdownMenuButton">
                                            <a style={{cursor:"pointer"}} onClick={()=>{history.push('/Quotations/Edit_Quotation')}} class="dropdown-item">Edit</a>
                                            <a class="dropdown-item" href="#">Delete</a>
                                            </div>
                                            </div>
                                        </td>  */}
                                    </tr>
                                
                                
                                </tbody>
                            </table>
                    </div>
                </div>
                    </div>
            </React.Fragment>
         );
    }
}
 
export default QutationTable;