import React, { Component } from "react";
import { Pie } from "react-chartjs-2";

const data = {
  labels: ["Electricity Expenses", "Transpotation expenses", "Income"],
  datasets: [
    {
      data: [300, 100, 100],
      backgroundColor: ["#f8a100", "#fc4349", "#13CE7D"],
      hoverBackgroundColor: ["#f8a100", "#fc4349", "#13CE7D"]
    }
  ]
};
const options={
      legend:{
          display: true,
          position:'right'
      }
  };

// const options = {
//   labels: {
//     display: false
//   }
// };
class test extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="row">
          <div className="col-12">
            <Pie data={data} options={options} />
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default test;
