import React, { Component } from 'react';
import { Doughnut } from 'react-chartjs-2';

import history from '../../Routing/history';

const data = {
    labels: ['Food','Electricity','Transportation',],
    datasets: [
      {
        label: 'Rainfall',
        backgroundColor: [
          '#13CE7D',
          '#FFA500',
          '#FC4349',
        ],
        // hoverBackgroundColor: [
        // '#501800',
        // '#4B5000',
        // '#175000',
        // '#003350',
        // '#35014F'
        // ],
        data: [4,2,3]
      }
    ]
  }

const options={
      legend:{
          display: true,
          position:'right'
      }
  };

class DoughnutChart extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid shadow-sm rounded-lg h-100">
                <div className="row">
                    <div className="col-6 dashboard-line-chart-title">
                        {this.props.title}
                    </div>
                    <div className="col-6 text-right">
                        <div class="dropdown">
                            <button type="button" class="btn dashboard-line-chart-dropdown-btn dropdown-toggle" data-toggle="dropdown">
                                Year To Date
                            </button>
                            <div class="dropdown-menu dashboard-line-chart-dropdown-box shadow-lg">
                                <a class="dropdown-item" href="#">This Quarter</a>
                                <a class="dropdown-item" href="#">Last Month</a>
                                <a class="dropdown-item" href="#">Select Date</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div onClick={()=>history.push('/Expense')}>
                    <div className="row">
                        <div className="col-12">
                            <div className="row">
                                <div className="col-12 dashboard-line-chart-amount">
                                    €&nbsp;{this.props.amount}
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12 dashboard-line-chart-interval">
                                    BUSINESS SPENDING
                                </div>
                            </div>
                            
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <Doughnut
                                ref="chart"
                                data={data}
                                options={options}
                                // height={200}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default DoughnutChart; 