import '../../Assets/Styles/common.css';
import './navbar.css';

import React, { Component } from 'react';

import history from '../../Routing/history';

class Navbar extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid nav-bg shadow-sm">
                <div className="row">
                    <div className="col-12">
                        <img 
                            alt="menu"
                            className="nav-menu-icon"
                            src={require('../../Assets/Icons/Navbar/menu.svg')}
                            onClick={()=>this.props.handleCollapse()}
                        />
                        <div className="nav-action-container">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-4 text-center">
                                        <img
                                            onClick={()=>history.push('/Setting')}
                                            alt="setting"
                                            className="nav-action-icon"
                                            src={require('../../Assets/Icons/Navbar/setting.png')}
                                        />
                                    </div>
                                    <div className="col-4 text-center">
                                        <img
                                            alt="setting"
                                            className="nav-action-icon"
                                            src={require('../../Assets/Icons/Navbar/help.png')}
                                        />
                                    </div>
                                    <div className="col-4 text-center">
                                        <img
                                            id="nav-profile-icon"
                                            alt="setting"
                                            className="nav-action-icon"
                                            src={require('../../Assets/Icons/Navbar/profile.png')}
                                        />
                                        <div onClick={()=>history.push('/')} className="nav-logout shadow">
                                            Logout
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {
                            this.props.setting === "yes"?
                            ""
                            :
                            <button 
                                className="btn nav-invoice-btn"
                                onClick={()=>history.push('/Invoices/Add_Invoice')}
                            >
                                Generate Invoice
                            </button>
                        }
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default Navbar;