import './billView.css';
import React, { Component } from 'react';

import Sidebar from '../../Components/Sidebar/sidebar';
import Navbar from '../../Components/Navbar/navbar';
import BillList from '../../Components/Bill/Bills/billForm';
// import InvoiceList from '../../Components/Invoice/All-Invoice/allInvoice';

class InvoiceView extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="w-100">
                <div 
                    className={
                        this.props.collapse?
                        "common-view-sidebar-close"
                        :
                        "common-view-sidebar-open"
                    }
                >
                    <Sidebar
                        backgroundColor="#4C4C53"
                        collapse={this.props.collapse}
                        handleCollapse={this.props.handleCollapse}
                        selected={3}
                        expandExpense={true}
                        expenseSelected={2}
                    />
                </div>
                <div 
                    className={
                        this.props.collapse?
                        "common-view-body-close"
                        :
                        "common-view-body-open"
                    }
                >
                    <div className="bill-view-green-bg">
                        <div className="bill-view-bg">
                            <div style={{padding:"0"}} className="container-fluid">
                                <div className="common-view-body-scroll">
                                    <div className="row sticky-top">
                                        <div className="col-12">
                                            <Navbar
                                                handleCollapse={this.props.handleCollapse}
                                            />
                                        </div>
                                    </div>
                                    <div className="row pl-4 pr-4 pb-3">
                                        {/* <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1"/> */}
                                        {/* <div className="col-12 col-sm-12 col-md-12 col-lg-10 col-xl-10"> */}
                                        <div className="col-12">
                                            <div className="row mt-4">
                                                <div className="col-12">
                                                    <BillList/>
                                                </div>
                                            </div>
                                        </div>
                                        {/* <div className="col-0 col-sm-0 col-md-0 col-lg-1 col-xl-1"/> */}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default InvoiceView;