import React, { Component } from "react";
//            Importing CSS Files
import "./profitAndLoss.css";
//            Importing Components
import ProfitAndLossChart from "./profitAndLossChart";

const data = {
  labels: [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ],
  datasets: [
    {
      label: "",
      fill: false,
      lineTension: 0,
      backgroundColor: "#29A281",
      borderColor: "#71C898",
      borderCapStyle: "butt",
      borderDash: [],
      borderDashOffset: 0.0,
      borderJoinStyle: "miter",
      pointBorderColor: "#71C898",
      pointBackgroundColor: "#2D9A5E",
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBackgroundColor: "#71C898",
      pointHoverBorderColor: "rgba(220,220,220,1)",
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      barPercentage: 0.4,
      data: [65, 59, 80, 81, 56, 55, 40, 80, 81, 56, 55, 40, 20],
    },
  ],
};

class ProfitAndLossGraph extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid rounded shadow-sm pt-2 pb-2 pl-5 pr-5">
          <div
            data-toggle="collapse"
            data-target="#summary"
            className="row cursor-pointer"
          >
            <div className="col-12">
              <div className="profit-loss-body-head-title">
                Income and Expense Summary
              </div>
              <img
                alt="down"
                className="profit-loss-body-head-down-icon"
                src={require("../../Assets/Icons/ProfitAndLoss/down-arrow.svg")}
              />
            </div>
          </div>
          <div id="summary" className="row mt-5">
            <div className="col-12 col-sm-12 col-md-4 col-lg-3 col-xl-3">
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12 profit-loss-body-net-profit-amount">
                      $ 122,200.21
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 profit-loss-body-net-profit-text">
                      NET PROFIT
                    </div>
                  </div>
                </div>
              </div>
              <div className="row profit-loss-body-income-box">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12 profit-loss-body-income-amount">
                      $345,600.54
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 profit-loss-body-income-text">
                      INCOME
                    </div>
                  </div>
                </div>
              </div>
              <div className="row mt-2 profit-loss-body-expense-box">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12 profit-loss-body-expense-amount">
                      $345,600.54
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12 profit-loss-body-expense-text">
                      INCOME
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-sm-12 col-md-8 col-lg-9 col-xl-9">
              {/* <Bar ref="chart" data={data} height={70} /> */}
              <ProfitAndLossChart />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default ProfitAndLossGraph;
