import React, { Component } from 'react';
import './editInvoiceForm.css';

class EditInvoiceForm extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
            <div className="container-fluid shadow-sm edit-invoice-container mt-4 mb-4">
                <div className="row">
                    <div className="col-12">
                        <div className="edit-invoice-header-text"><p>Edit Invoice</p></div>
                        <button className="edit-invoice-header-delete"><img alt="delete-pic" src={require('../edit-invoice-form/delete.svg')} style={{height:"30px",width:"30px",boxShadow:"none",paddingBottom:"5px"}}></img></button>
                        </div>
                    <div className="col-xl-5 col-lg-5 col-md-6 col-sm-12 col-12">
                        <div className="edit-invoice-logo-block"><img alt="add-pic" src={require('../edit-invoice-form/blackplus.svg')} style={{width:"30px",height:"30px",marginLeft:"20px",marginRight:"20px"}}></img>Add Your Logo</div>
                        <div className="edit-invoice-text-feild"><input type="text" placeholder="Who is this invoice from ?(required)" required></input></div>
                        <span className="edit-invoice-span-text">Bill To</span>
                        <div className="edit-invoice-text-feild2"><input type="text" placeholder="Who is this invoice from ?(required)" required></input></div>
                    </div>
                    <div className="col-xl-2 col-lg-2 col-md-0 col-sm-0 col-0" />
                    <div className="col-xl-5 col-lg-5 col-md-6 col-sm-12 col-12 text-right pr-4">
                        <p className="edit-invoice-form-header-text">Invoice</p>
                        <div className="edit-invoice-form-number"><input type="number" placeholder="#" className="text-left"></input></div>
                        <div className="edit-invoice-form-date"><span>Data</span><input type="number" placeholder="Mar 9,2020"></input></div>
                        <div className="edit-invoice-form-time"><span>Time</span><input type="number" placeholder="Mar 9,2020"></input></div>
                    </div>
                    <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 pr-5 pl-5">
                        <table className="table table-borderless mt-3">
                            <tbody>
                                <tr className="text-white" style={{backgroundColor:"#3d3d3d"}}>
                                    <td><span className="edit-invoice-tble-head">Item</span></td>
                                    <td className="edit-invoice-tbl-description"><span className="edit-invoice-tble-head">Description</span></td>
                                    <td><span className="edit-invoice-tble-head">Qty</span></td>
                                    <td><span className="edit-invoice-tble-head">Amount</span></td>
                                    <td><span className="edit-invoice-tble-head">Total</span></td>
                                </tr>
                                <tr>
                                    <td><input type="text" placeholder="Item name..."></input></td>
                                    <td className="edit-invoice-tbl-description"><input type="text" placeholder="Enter Description..."></input></td>
                                    <td><input type="number" placeholder="Qty..."></input></td>
                                    <td><input type="number" placeholder="Amount..."></input></td>
                                    <td><input type="number" placeholder="Total..."></input></td>
                                </tr>
                                <tr>
                                    <td><button className="edit-invoice-header-btn"><img alt="white-plus-pic" src={require('../edit-invoice-form/plus.svg')} style={{height:"15px",width:"15px",boxShadow:"none",margin:"-3px 20px 0 0"}}></img>Line Item</button></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="col-12 text-right">
                        <div className="edit-invoice-total-main">
                            <div className="edit-invoice-total-head">
                                <span className="edit-invoice-total-text">Sub Total</span>
                                <span className="edit-invoice-total-amount">$0.00</span>
                            </div>
                            <br></br>
                            <button className="edit-invoice-header-btn"><img alt="white-plus-pic" src={require('../edit-invoice-form/plus.svg')} style={{height:"15px",width:"15px",boxShadow:"none",margin:"-3px 20px 0 0"}}></img>Line Item</button>
                            <br></br>
                            <div className="edit-invoice-total-head1">
                                <span className="edit-invoice-total-text1">Total</span>
                                <span className="edit-invoice-total-amount1">$0.00</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-12 ml-4">
                        <div className="edit-invoice-note-text">Notes</div>
                        <div className="edit-invoice-note-text-field"><input type="text" placeholder="Notes- any relevent information not already covered"></input></div>
                        <button className="edit-invoice-header-btn float-left"><img alt="white-plus-pic" src={require('../edit-invoice-form/plus.svg')} style={{height:"15px",width:"15px",boxShadow:"none",margin:"-3px 20px 0 0"}}></img>Line Item</button>
                    </div>
                </div>
            </div>
            </React.Fragment>
         );
    }
}
 
export default EditInvoiceForm;