import '../../Assets/Styles/common.css';
import './settingView.css';
import React, { Component } from 'react';

import Sidebar from '../../Components/Sidebar/sidebar';
import Navbar from '../../Components/Navbar/navbar';

import Setting from '../../Components/Setting/setting';

class SettingView extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="w-100">
                <div 
                    className={
                        this.props.collapse?
                        "common-view-sidebar-close"
                        :
                        "common-view-sidebar-open"
                    }
                >
                    <Sidebar
                        backgroundColor="#13CE7D"
                        collapse={this.props.collapse}
                        handleCollapse={this.props.handleCollapse}
                        selected={0}
                        expandExpense={false}
                        expenseSelected={0}
                    />
                </div>
                <div 
                    className={
                        this.props.collapse?
                        "common-view-body-close"
                        :
                        "common-view-body-open"
                    }
                >
                    <div className="setting-view-green-bg">
                        <div style={{padding:"0"}} className="container-fluid setting-view-bg">
                            <div className="common-view-body-scroll">
                                <div className="row sticky-top">
                                    <div className="col-12">
                                        <Navbar
                                            handleCollapse={this.props.handleCollapse}
                                            setting="yes"
                                        />
                                    </div>
                                </div>
                                <div className="row mt-4 pl-4 pr-4 pb-4">
                                    <div className="col-12">
                                        <Setting/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default SettingView;