import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';

const data = {
    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
    datasets: [
      {
        label: '',
        fill: false,
        lineTension: 0,
        backgroundColor: 'rgba(75,192,192,0.4)',
        borderColor: '#71C898',
        borderCapStyle: 'butt',
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: 'miter',
        pointBorderColor: '#71C898',
        pointBackgroundColor: '#2D9A5E',
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: '#71C898',
        pointHoverBorderColor: 'rgba(220,220,220,1)',
        pointHoverBorderWidth: 2,
        pointRadius: 5,
        pointHitRadius: 10,
        data: [65, 59, 80, 81, 56, 55, 40]
      }
    ]
  };

class LineChart extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="container-fluid shadow-sm rounded-lg">
                <div className="row">
                    <div className="col-6 dashboard-line-chart-title">
                        {this.props.title}
                    </div>
                    <div className="col-6 text-right">
                        <div class="dropdown">
                            <button type="button" class="btn dashboard-line-chart-dropdown-btn dropdown-toggle" data-toggle="dropdown">
                                This Quarter
                            </button>
                            <div class="dropdown-menu dashboard-line-chart-dropdown-box shadow-lg">
                                <a class="dropdown-item" href="#">This Quarter</a>
                                <a class="dropdown-item" href="#">Last Month</a>
                                <a class="dropdown-item" href="#">Select Date</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-3">
                        <div className="row">
                            <div className="col-12 dashboard-line-chart-amount">
                                €&nbsp;{this.props.amount}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 dashboard-line-chart-interval">
                                This Quarter
                            </div>
                        </div>
                        
                    </div>
                    <div className="col-9">
                        <Line
                            ref="chart"
                            data={data}
                            height={200}
                        />
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default LineChart; 