import '../../Assets/Styles/common.css';
import './dashboardView.css';

import React, { Component } from 'react';

import history from '../../Routing/history'

import Sidebar from '../../Components/Sidebar/sidebar';
import Navbar from '../../Components/Navbar/navbar';
import Invoice from '../../Components/Dashboard/invoice';
import DoughnutChart from '../../Components/Dashboard/doughnutChart';
import ProfitLoss from '../../Components/Dashboard/profitLoss';
import LineChart from '../../Components/Dashboard/lineChart';
import BarChart from '../../Components/Dashboard/barChart';

class DashboardView extends Component {
    state = {  }
    render() { 
        return ( <React.Fragment>
            <div className="w-100">
                <div 
                    className={
                        this.props.collapse?
                        "common-view-sidebar-close"
                        :
                        "common-view-sidebar-open"
                    }
                >
                    <Sidebar
                        backgroundColor="#13CE7D"
                        collapse={this.props.collapse}
                        handleCollapse={this.props.handleCollapse}
                        selected={1}
                        expandExpense={false}
                        expenseSelected={0}
                    />
                </div>
                <div 
                    className={
                        this.props.collapse?
                        "common-view-body-close"
                        :
                        "common-view-body-open"
                    }
                >
                    <div className="dashboard-view-green-bg">
                        <div style={{padding:"0"}} className="container-fluid dashboard-view-bg">
                            <div className="common-view-body-scroll">
                                <div className="row sticky-top">
                                    <div className="col-12">
                                        <Navbar
                                            handleCollapse={this.props.handleCollapse}
                                        />
                                    </div>
                                </div>
                                <div className="row pl-4 pr-4">
                                    <div className="col-12">
                                        <div className="row mt-4">
                                            <div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4 cursor-pointer">
                                                <Invoice/>
                                            </div>
                                            <div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-4 cursor-pointer dashboard-view-margin-top-767px">
                                                <DoughnutChart
                                                    title="Expenses"
                                                    amount="87,540"
                                                />
                                            </div>
                                            <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-4 dashboard-view-margin-top-1200px">
                                                <ProfitLoss/>
                                            </div>
                                        </div>
                                        <div className="row mt-4">
                                            <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-6">
                                                <BarChart
                                                    title="Sales"
                                                    amount="467,121"
                                                />
                                            </div>
                                            <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-6 dashboard-view-margin-top-1200px">
                                                <BarChart
                                                    title="Expenses"
                                                    amount="467,121"
                                                />
                                            </div>
                                        </div>
                                        <div className="row mt-4 pb-3">
                                            <div className="col-12 col-sm-12 col-md-10 col-lg-9 col-xl-8">
                                                <LineChart
                                                    title="Bank Balance"
                                                    amount="467,121"
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment> );
    }
}
 
export default DashboardView;