import React, { Component } from 'react';
import "./signUp.css";
import history from '../../Routing/history';
import UserImage from "../../Assets/Icons/SignIn/Icon ionic-ios-person.png";
import CompanyImage from "../../Assets/Icons/SignIn/Icon awesome-building.png";
import EmailIcon from "../../Assets/Icons/SignIn/Icon ionic-ios-mail.png";
import MobileIcon from "../../Assets/Icons/SignIn/Icon feather-smartphone.png";
import ColmnImage from "../../Assets/Icons/SignIn/NoPath - Copy (10).png";
import LockIcon from "../../Assets/Icons/SignIn/Icon awesome-lock.png";
class SignUP extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
                <div className="container-fluid sign-up-container bg-white">
                    <div className="row  pl-4 ">
                        <div className="col-xl-12 Sign-up-heading">
                            Sign up
                        </div>
                    </div>
                   <div className="row p-4">
                    <div className="col-xl-7 col-lg-7 col-md-12 col-sm-12 col-12 pl-4">
                    <div className="row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                        <div class="left-inner-addon input-container">
                        <img src={UserImage}/>
                        <input type="text"  class="Sign-up-input-fields " placeholder="First Name" />
                        </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                            <div class="left-inner-addon input-container">
                            <img src={UserImage}/>
                        <input type="text" class="Sign-up-input-fields" placeholder="Last Name" />
                        </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                        <div class="left-inner-addon input-container">
                        <img src={CompanyImage}/>
                        <input type="text" class="Sign-up-input-fields " placeholder="Company Name" />
                        </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                            <div class="left-inner-addon input-container">
                            <img src={CompanyImage}/>
                        <input type="text" class="Sign-up-input-fields " placeholder="Company Reg Number" />
                        </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                        <div class="left-inner-addon input-container">
                        <img src={EmailIcon}  className="mt-2"/>
                        <input type="text" class="Sign-up-input-fields " placeholder="Email" />
                        </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                            <div class="left-inner-addon input-container">
                            <img src={MobileIcon}/>
                        <input type="text" class="Sign-up-input-fields " placeholder="Phone Number" />
                        </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                        <div class="left-inner-addon input-container">
                        <img src={LockIcon}/>
                        <input type="text" class="Sign-up-input-fields" placeholder="Password" />
                        </div>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
                            <div class="left-inner-addon input-container">
                        <img src={LockIcon}/>
                        <input type="text" class="Sign-up-input-fields" placeholder="Confirm Password" />
                        </div>
                        </div>
                        <div className="row w-100">
                            <div className="col-xl-12 col-lg-12 col-md-6 col-sm-12">
                            <label class="login-page-checkbox-container float-left"><span className="Sign-up-agree-text">I agree all statements in <a herf="#" className="term-and-service-text">Terms of services</a></span>
                        <input type="checkbox" />
                        <span class="new-created-check-box"></span>
                        </label><br/>
                      
                            </div>
                            <div className="row pl-3">
                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                                <div className="text-left">
                                <button onClick={()=>history.push('/')} className="sign-up-Register-button">Register</button>
                                </div>
                                </div>
                            </div>
                        </div>

                    </div>
                    </div>
                    <div className="col-xl-5 col-lg-5 col-md-none col-sm-none col-none sign-up-col-image text-center">
                        <img src={ColmnImage} width="60%"/><br/>
                        <a onClick={()=>history.push('/')} className="Already-have-an-account">Already have an account?</a>
                    </div>
                    <div className="row mt-5 w-100 text-center">
                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                                <div >
                                <a onClick={()=>history.push('/')} className="Already-have-an-account-show-after-hide-image">Already have an account?</a>
                                </div>
                                </div>
                            </div>
                    </div> 
                </div>
            </React.Fragment>
         );
    }
}
 
export default SignUP;