import React, { Component } from "react";
import history from "./history";
import { Route, Switch, Router } from "react-router-dom";

import SignInView from "../Views/SignIn/signInView";
import SignUpView from "../Views/Signup/signupView";
import DashboardView from "../Views/Dashboard/dashboardView";
import InvoiceView from "../Views/Invoice/invoiceView";
import BillView from "../Views/Bill/billView";
import QuotationView from "../Views/Quotation/quotationView";
import EditQuotationView from "../Views/EditQuotation/editQuotationView";
// import GetQuotationView from '../Views/GetQoutation/getQuotationView';
import ExpenseView from "../Views/Expense/ExpansisChartView/expensisView";
import AddNewInvoiceView from "../Views/AddNewInvoice/addNewInvoiceView";
import GetQuotationView from "../Views/AddInvoice/addInvoiceView";
import EditInvoiceView from "../Views/EditInvoice/editInvoiceView";
import SettingView from "../Views/Setting/settingView";
import ProfiAndLossView from "../Views/ProfitAndLoss/profitAndLossView";

class Routing extends Component {
  state = {
    collapse: false,
  };
  handleCollapse = () => {
    this.setState({
      collapse: !this.state.collapse,
    });
  };
  render() {
    return (
      <React.Fragment>
        <Router history={history}>
          <Switch>
            <Route
              exact
              path="/"
              render={(props) => <SignInView {...props} />}
            />
            <Route
              path="/Signup"
              render={(props) => <SignUpView {...props} />}
            />
            <Route
              path="/Dashboard"
              render={(props) => (
                <DashboardView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Invoice"
              render={(props) => (
                <InvoiceView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Invoices/Add_Invoice"
              render={(props) => (
                <AddNewInvoiceView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Invoices/Edit_Invoice"
              render={(props) => (
                <EditInvoiceView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Bill"
              render={(props) => (
                <BillView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Quotation"
              render={(props) => (
                <QuotationView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Quotations/Edit_Quotation"
              render={(props) => (
                <EditQuotationView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Quotations/Get_Quotation"
              render={(props) => (
                <GetQuotationView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Expense"
              render={(props) => (
                <ExpenseView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Setting"
              render={(props) => (
                <SettingView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
            <Route
              path="/Profit_Loss"
              render={(props) => (
                <ProfiAndLossView
                  collapse={this.state.collapse}
                  handleCollapse={this.handleCollapse}
                  {...props}
                />
              )}
            />
          </Switch>
        </Router>
      </React.Fragment>
    );
  }
}

export default Routing;
